.home-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #e0eafc 0%, #cfdef3 100%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; /* Center content vertically */
  font-family: 'Segoe UI', Arial, sans-serif;
  padding-top: 60px;
  box-sizing: border-box;
}

.main-content {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.dropdown-container {
  background: #fff;
  padding: 32px 40px;
  border-radius: 16px;
  box-shadow: 0 4px 24px rgba(0,0,0,0.08);
  min-width: 340px;
}

.dropdown-group {
  margin-bottom: 22px;
}

.dropdown-group label {
  display: block;
  margin-bottom: 6px;
  color: #1976d2;
  font-weight: 500;
}

.dropdown {
  width: 100%;
  padding: 10px 12px;
  border: 1px solid #b0bec5;
  border-radius: 6px;
  font-size: 1rem;
  background: #f7fafc;
  transition: border 0.2s;
}

.dropdown:focus {
  border-color: #1976d2;
  outline: none;
}

.submit-btn {
  width: 100%;
  padding: 12px 0;
  background: #1976d2;
  color: #fff;
  border: none;
  border-radius: 6px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s;
  margin-top: 10px;
}

.submit-btn:disabled {
  background: #b0bec5;
  cursor: not-allowed;
}

.footer {
  margin: 32px 0 12px 0;
  color: #888;
  font-size: 0.95rem;
  text-align: center;
}

/* Spinner for dropdowns */
.dropdown-spinner {
  display: inline-block;
  width: 18px;
  height: 18px;
  border: 3px solid #1976d2;
  border-radius: 50%;
  border-top: 3px solid #fff;
  animation: spin 0.8s linear infinite;
  vertical-align: middle;
  margin-right: 8px;
}
@keyframes spin {
  0% { transform: rotate(0deg);}
  100% { transform: rotate(360deg);}
}

/* Responsive adjustments for home page */
@media (max-width: 480px) {
  .home-container {
    padding-top: 50px;
  }
}